import {Checkbox, FormControlLabel, Grid, Typography} from "@mui/material";
import classes from './PaymentPage.module.css'
import PaymentType from "../../components/PaymentType";
import Exchange from "../../components/ExchangeCurrencies";
import Requisites from "../../components/Requisites";

export const PaymentPage = () => <Grid container direction='column' spacing={2}>
    <Typography className={classes.typographyTitle}> </Typography>
    <PaymentType/>
    <Typography className={classes.typographyTitle}></Typography>
    <Exchange/>
    <Grid container justifyContent='space-between'>
        <Typography className={classes.typographyTitle}></Typography>
        <FormControlLabel control={<Checkbox defaultChecked/>} className={classes.formControlLabel}
                          label={<Typography className={classes.label}> </Typography>}
                          labelPlacement='start'/>
    </Grid>
    <Requisites/>
</Grid>